import { BUILD_WORD_KEY } from './../../../../shared/utils/builWordKey';
import { ApiPaths } from './../../../../shared/enuns/api-paths.enum';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { MsgService } from 'src/app/core/menssages/msg.service';
import { ApiService } from 'src/app/core/services/api.service';
import { BreadcrumbService } from 'src/app/shared/config/app.breadcrumb.service';
import { TOAST_SUCCESSO } from 'src/app/core/menssages/toastEvent';
import { SAVE_SUCESS } from 'src/app/core/menssages/menssagens';

@Component({
  selector: 'app-type-maintenance',
  templateUrl: './type-maintenance.component.html',
  styleUrls: ['./type-maintenance.component.scss']
})
export class RegisterTypeMaintenanceComponent implements OnInit {

  public typeMaintenanceFormGroup: FormGroup;

  constructor(
    private formBuilder: FormBuilder,
    private msg: MsgService,
    private spinner: NgxSpinnerService,
    private service: ApiService,
    private breadcrumbService: BreadcrumbService
  ) {
    this.breadcrumbService.setItems([{ label: 'Registrar tipo de Despesa'}]);
  }

  private loadFormGroup(): void {
    this.typeMaintenanceFormGroup = this.formBuilder.group({
      name: ['', [Validators.required]],
      description: ['', [Validators.required]],
      liquid: ['', [Validators.required]],
      targeting: ['', [Validators.required]],
      wordKey: ['']
    });
  }

  public register(){

    this.spinner.show();

    this.typeMaintenanceFormGroup.value.wordKey = BUILD_WORD_KEY(this.typeMaintenanceFormGroup.value);
    this.service.post<void>(ApiPaths.TYPE_EXPENSE,
                            this.typeMaintenanceFormGroup.value)
                .subscribe(() => {
                  this.spinner.hide();
                  this.msg.toast(TOAST_SUCCESSO(SAVE_SUCESS));
                  this.typeMaintenanceFormGroup.reset();
                });
  }

  ngOnInit(): void {
    this.loadFormGroup();
  }
}
