import { ApiPaths } from 'src/app/shared/enuns/api-paths.enum';
import { HttpParams } from '@angular/common/http';
import { LoginCookieStorage } from './../../../../core/cookieStorage/loginCookieStorage';
import { element } from 'protractor';
import { BUILD_PAYLOAD_EMPLOYEE } from './build_payload';
import { DeviceType } from '../../../../shared/utils/device-type';
import { CrudRegisterDriverStorage } from '../../../../shared/storage/crudStorageDriverRegister';
import { Router } from '@angular/router';
import { CepService } from '../../../../core/services/cep.service';
import { ValidateDate } from '../../../../shared/utils/validateDate';
import {
  DRIVER_TYPE_DEFAULTS,
  GENDERS,
  MARITAL_STATUS,
} from './constants-employee';
import {
  SAVE_SUCESS,
} from '../../../../core/menssages/menssagens';
import { ValidateDoc } from '../../../../shared/utils/validateDoc';
import { ApiService } from '../../../../core/services/api.service';
import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { Subscription } from 'rxjs';
import { MsgService } from 'src/app/core/menssages/msg.service';
import { TOAST_ERROR, TOAST_SUCCESSO } from 'src/app/core/menssages/toastEvent';
import { BreadcrumbService } from 'src/app/shared/config/app.breadcrumb.service';
import { MatStepper } from '@angular/material/stepper';
import { HostListener } from '@angular/core';
import { ElementsResponse, Page } from 'src/app/shared/model/response/elementsResponse';
import { ImgService } from '../../img/img.service';
import { SimpleData } from 'src/app/shared/model/simple_data';

@Component({
  selector: 'app-register-employee',
  templateUrl: './register-employee.component.html',
  styleUrls: ['./register-employee.component.scss'],
})
export class RegisterEmployeeComponent implements OnInit {
  public personalDataFormGroup: FormGroup;
  public professionalDataFormGroup: FormGroup;
  public contactFormGroup: FormGroup;
  public addressFormGroup: FormGroup;
  public docFormGroup: FormGroup;
  public alertFormGroup: FormGroup;
  public genders = GENDERS;
  private subscription = new Subscription();
  public maritalStatus: any = MARITAL_STATUS;
  public phones: FormArray;
  public allowLogin: boolean = false;
  public perfil: string = '';

  public mobile = DeviceType.mobileType();

  public alerts: FormArray;
  public alertSh: boolean;
  public office = '';
  public pageTypeEmployee = new Page();
  public pagePerfil = new Page();
  public id: string;
  public functions = [];
  public apiPathFile = ApiPaths.EMPLOYEE_FILES;
  public perfis = [];

  public selectedMtStatus;
  public isLinear = false;
  public stepper: MatStepper;

  constructor(
    private formBuilder: FormBuilder,
    private msg: MsgService,
    private spinner: NgxSpinnerService,
    private service: ApiService,
    public loginCookieStorage: LoginCookieStorage,
    public router: Router,
    private cep: CepService,
    public imgService: ImgService,
    private breadcrumbService: BreadcrumbService
  ) {
    this.breadcrumbService.setItems([{ label: 'Registrar Colaborador' }]);
  }

  private loadFormGroup(): void {
    this.personalDataFormGroup = this.formBuilder.group({
      name: ['', [Validators.required]],
      gender: ['', [Validators.required]],
      maritalStatus: ['', [Validators.required]],
      birthday: ['', [Validators.required, ValidateDate.validateDateString]],
      cpf: ['', [Validators.required, ValidateDoc.validateCPF]],
    });

    this.contactFormGroup = this.formBuilder.group({
      phones: this.formBuilder.array([this.createFormPhone()]),
      email: ['', Validators.email],
    });

    this.addressFormGroup = this.formBuilder.group({
      postalCode: ['', [Validators.required]],
      street: ['', [Validators.required]],
      number: ['', [Validators.required]],
      neighborhood: ['', [Validators.required]],
      complement: [''],
      city: ['', [Validators.required]],
      state: ['', [Validators.required]],
    });

    this.professionalDataFormGroup = this.formBuilder.group({
      cnpj: ['', [Validators.required, ValidateDoc.validateCNPJ]],
      note: [''],
    });

    this.docFormGroup = this.formBuilder.group({
      rg: ['', [Validators.required]],
      rgEmitter: ['', [Validators.required]],
      rgDateEmitter: ['', [Validators.required]],
      motherName: ['', [Validators.required]],
    });
  }

  public cancelAlert() {
    this.alertSh = false;
    this.alertFormGroup.reset();
  }

  public cancelConfigUser() {
    this.perfil = '';
    this.allowLogin = false;
  }

  public allowPerfil(){
    this.perfil = '';
    this.allowLogin = true;
  }

  public ifTypeEmployeeDrivre() {
    if(this.office.toLowerCase().indexOf('motorista') > -1){
      return true;
    }

    return false;
  }

  public createFormPhone(): FormGroup {
    return this.formBuilder.group({
      phone: ['', [Validators.required]],
    });
  }

  public addPhone(): void {
    this.phones = this.contactFormGroup.get('phones') as FormArray;
    this.phones.push(this.createFormPhone());
  }

  public removePhone(i): void {
    this.phones.removeAt(i);
  }

  public reset(stepper) {
    this.loadFormGroup();
    stepper.reset();
  }

  public keyUpcep(cep): void {
    if (cep.length == 9) {
      this.spinner.show();
      this.cep.get(cep).subscribe((r) => {
        if (!r.cep) {
          this.msg.toast(TOAST_ERROR('CEP não encontrado!'));
        }
        this.spinner.hide();
        this.addressFormGroup.get('city').setValue(r.city);
        this.addressFormGroup.get('neighborhood').setValue(r.neighborhood);
        this.addressFormGroup.get('state').setValue(r.state);
        this.addressFormGroup.get('street').setValue(r.street);
      });
    }
  }

  public driverRegister(): void {

    this.spinner.show();
    this.service.post<void>(`${ApiPaths.EMPLOYEE}`, BUILD_PAYLOAD_EMPLOYEE(this))
                .subscribe((r: any) => {
        this.spinner.hide();

        this.msg.toast(TOAST_SUCCESSO(SAVE_SUCESS));

        CrudRegisterDriverStorage.set(r.id);
        CrudRegisterDriverStorage.setType(this.office);

        this.router.navigate(['register/employee/driver']);

      });
  }

  /* end */

  public scrollToEndTypeEmployee() {

    if(this.functions.length < this.pageTypeEmployee.totalElements){
      this.service.get(ApiPaths.TYPE_EMPLOYEE, {page: this.pageTypeEmployee.nextPage, size: this.pageTypeEmployee.size})
      .subscribe((r: ElementsResponse) => {

        this.pageTypeEmployee = r.page;

        r.elements.forEach((element) => {
          this.functions.push(element);
         });
      });
    }
  }

  public loadTypeEmployee(stepper){
    this.spinner.show();
    this.service.get(ApiPaths.TYPE_EMPLOYEE, {page: 0, size: 10})
                .subscribe((r: ElementsResponse) => {
                   this.pageTypeEmployee = r.page;
                   stepper.next();
                   this.functions = r.elements;
                     this.spinner.hide();
                });
  }

  public scrollToEndPerfis() {

    if(this.perfis.length < this.pageTypeEmployee.totalElements){
      this.service.get(ApiPaths.PERFIL, {page: this.pagePerfil.nextPage, size: this.pagePerfil.size})
      .subscribe((r: ElementsResponse) => {

        this.pagePerfil = r.page;

        r.elements.forEach((element) => {
          this.perfis.push(element);
         });
      });
    }
  }

  public loadPerfis(stpper){
    this.spinner.show();
    this.service.get(ApiPaths.PERFIL, {page: 0, size: 10})
                .subscribe((r: ElementsResponse) => {
                   stpper.next()
                   this.pagePerfil = r.page;
                   this.perfis = r.elements;
                     this.spinner.hide();
                });
  }

  public register(stepper?): void {
    this.spinner.show();
    this.professionalDataFormGroup.value.office = this.office;
    const sub = this.service
                    .post<SimpleData>(`${ApiPaths.EMPLOYEE}`, BUILD_PAYLOAD_EMPLOYEE(this))
                    .subscribe((r) => {
                       this.id = r.id;
          stepper.next()

        this.spinner.hide();
        this.msg.toast(TOAST_SUCCESSO(SAVE_SUCESS));
      });
    this.subscription.add(sub);
  }

  ngOnInit(): void {
    this.loadFormGroup();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}
