import { ApiService } from '../../core/services/api.service';
import { ApiPaths } from './../enuns/api-paths.enum';
import { debounceTime, map, startWith} from 'rxjs/operators';
import { fromEvent, Observable } from 'rxjs';
import { Injectable, OnInit } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class AutoCompleteService{

 public filteredOptions: Observable<any[]>;

  constructor(private service: ApiService) {}

  public loadautoComplete(doc){

    const searchBox = doc.getElementById('search');
    const keyup$ = fromEvent(searchBox, 'keyup');

    keyup$.pipe( map((i: any) => i.currentTarget.value), debounceTime(300))
          .subscribe((r) => {

          if(!r){
            this.filteredOptions = null;
            return;
           }

          this.filteredOptions = this.service
                                   .search(`${ApiPaths.CAR_SEARCH}/${r}`)
                                   .pipe(map(x => x));
      });
  }
}
