
export class CrudRegisterDriverStorage {
  private static DVREG = 'DVREG';

  public static set(data: any): void {
    sessionStorage.setItem(CrudRegisterDriverStorage.DVREG, JSON.stringify(data));
  }

  public static get(): any {
   return JSON.parse(sessionStorage.getItem(CrudRegisterDriverStorage.DVREG));
  }

  public static remove(): void {
     sessionStorage.removeItem(CrudRegisterDriverStorage.DVREG);
  }


  private static TYPE_EMPLOYEE = 'TPL';

  public static setType(data: any): void {
    sessionStorage.setItem(CrudRegisterDriverStorage.TYPE_EMPLOYEE, JSON.stringify(data));
  }

  public static getType(): any {
   return JSON.parse(sessionStorage.getItem(CrudRegisterDriverStorage.TYPE_EMPLOYEE));
  }

  public static removeType(): void {
     sessionStorage.removeItem(CrudRegisterDriverStorage.TYPE_EMPLOYEE);
  }

}
