import { FormArray, FormGroup, Validators } from '@angular/forms';

export const CREATE_FORM_ALERT = function create($this): FormGroup {
  return $this.formBuilder.group({
    title: ['', [Validators.required]],
    date: ['', [Validators.required]],
    note: ['', [Validators.required]],
    priorityColor: ['', [Validators.required]],
    namePriority: ['', [Validators.required]],
    perfil: [''],
    usersIds: []
  });
}

export const ADD_ALERT = function add($this): void {
  $this.alerts = this.alertFormGroup.get('alerts') as FormArray;
  $this.alerts.push(CREATE_FORM_ALERT($this));
}

export const REMOVE_ALERT = function remove($this, i): void {
  $this.alerts.removeAt(i);
}

export const FORM_BUILDER_ALERT = function formBuild($this){
  return $this.formBuilder.group({
    alerts: $this.formBuilder.array([ CREATE_FORM_ALERT($this) ]),
  });
}

