import { AutoCompleteService } from './../../../shared/search/autocomplete';
import { ElementsResponse } from './../../../shared/model/response/elementsResponse';
import { Expenses } from './../../../shared/model/expenses';
import { Component, ElementRef, Inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { fromEvent, Observable, Subscription } from 'rxjs';
import { INPUT_VALIDATE_ERROR, SAVE_SUCESS } from 'src/app/core/menssages/menssagens';
import { MsgService } from 'src/app/core/menssages/msg.service';
import { TOAST_ERROR, TOAST_SUCCESSO } from 'src/app/core/menssages/toastEvent';
import { ApiService } from 'src/app/core/services/api.service';
import { ApiPaths } from 'src/app/shared/enuns/api-paths.enum';
import {debounceTime, map, startWith} from 'rxjs/operators';
import { TimerSharp } from '@material-ui/icons';
import { BreadcrumbService } from 'src/app/shared/config/app.breadcrumb.service';

const TYPES_ACTIVITY =  [
  {code: '', name: 'Selecionar tipo'},
  {code: 'INSPECTION', name: 'Inspeção'},
  {code: 'IUC', name: 'Imposto único de circulação'},
  {code: 'PIECE_EXCHANGE', name: 'Troca de peças'},
  {code: 'TRAFFIC_TICKET', name: 'Multa de trânsito'},
  {code: 'TIRE_CHANGE', name: 'Mudaça de pneus'},
  {code: 'VEHICLE_INSURANCE', name: 'Seguro do veículo'},
  {code: 'FUEL', name: 'Combustível'},
  {code: 'TOLL', name: 'Portagem'},
  {code: 'OTHER', name: 'Outros'}
];

@Component({
  selector: 'app-register-expenses',
  templateUrl: './register-expenses.component.html',
  styleUrls: ['./register-expenses.component.scss']
})
export class RegisterExpensesComponent implements OnInit {

  public registerFormGroup: FormGroup;
  public blockSubmit = false;
  public types = TYPES_ACTIVITY;
  private subscription = new Subscription();
  public carId: string;
  public fuel = false;
  cars = [];

  constructor(
    private formBuilder: FormBuilder,
    private msg: MsgService,
    private spinner: NgxSpinnerService,
    private service: ApiService,
    public autocompleService: AutoCompleteService,
    private breadcrumbService: BreadcrumbService) {
    this.breadcrumbService.setItems([{label: 'Registrar Despesa'}]);
  }

  private validatePayload(): void{

    if(this.fuel && !this.registerFormGroup.get('litros').value){
      throw new EvalError( this.msg.toast(TOAST_ERROR('Digite os litros para continuar')) );
    }

    if(!this.carId){
      throw new EvalError( this.msg.toast(TOAST_ERROR(INPUT_VALIDATE_ERROR)) );
    }

    if( this.registerFormGroup.invalid ){
      throw new EvalError( this.msg.toast(TOAST_ERROR(INPUT_VALIDATE_ERROR)) );
    }
  }

  private loadFormGroup(): void {
    this.registerFormGroup = this.formBuilder.group({
      type: ['', [Validators.required]],
      valor: ['',  [Validators.required]],
      description: ['', [Validators.required]],
      litros: ['']
     },
    );
  }

  private resetForm():void{
    this.registerFormGroup.reset();
    this.fuel = false;
  }

 public filter(event) {
    let query = event.query;
    this.service.get(`${ApiPaths.CAR_SEARCH}/${query}`, {}).subscribe((r: ElementsResponse) => {
      this.cars = this.buildResultfilter(query, r.elements);
    }, () => { this.cars = [] });
  }

  private buildResultfilter(query, r: any[]): any[] {
    let filtered: any[] = [];
    for (let i = 0; i < r.length; i++) {
      let value = r[i];
      if (value.vehicleRegistration.toLowerCase().indexOf(query.toLowerCase()) == 0) {
        filtered.push(value);
      }
    }
    return filtered;
  }

  public onSelectAutoComplete(value: any): any {
    setTimeout(() => {
     let c: any = document.getElementById('AutoC');
     c.value = value.vehicleRegistration;
    }, 1);
    this.carId = value.id;
  }

  public verifyTypeFuel( value: string ): void{
      if(value == 'FUEL'){this.fuel = true};
  }

  public register(): void{

    this.validatePayload();

    this.spinner.show();
    const sub = this.service.post<Expenses>( `${ApiPaths.EXPENSES}/${'5ef2c35a2313a7145beaa47f'}/${ApiPaths.CAR}/${this.carId}`, this.registerFormGroup.value )
                            .subscribe(() => {
                              this.spinner.hide();
                              this.resetForm();
                              this.msg.toast(TOAST_SUCCESSO(SAVE_SUCESS));
                             });
      this.subscription.add(sub);
  }

  ngOnInit(): void {
/*     this.autocompleService.loadautoComplete(document);
 */    this.loadFormGroup();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}
