import { Router } from '@angular/router';

const TYPE_VEHICLES = [ {value: 'CAR', name: 'Carro'},
                        {value: 'VAN', name: 'Carrinha'},
                        {value: 'TRUCK', name: 'Caminhão'},
                        {value: 'MOTORCYCLE', name: 'Motociclos'},
                        {value: 'OUTHER', name: 'Outros'}];

@Component({
  selector: 'app-register-vehicles',
  templateUrl: './register-vehicles.component.html',
  styleUrls: ['./register-vehicles.component.scss']
})
export class RegisterVehiclesComponent implements OnInit {

  public addControl = ADD_CONTROL;
  public removeControl = REMOVE_CONTROL;

  public addDetail = ADD_DETAIL;
  public removeDetail = REMOVE_DETAIL;

  public addAlert = ADD_ALERT;
  public removeAlert = REMOVE_ALERT;

  public vehicleFormGroup: FormGroup;
  public detailsFormGroup: FormGroup;
  public alertFormGroup: FormGroup;
  public controlsFormGroup: FormGroup;

  public details: FormArray;
  public controls: FormArray;
  public alerts: FormArray;

  public blockSubmit = false;
  public type_vehicles = TYPE_VEHICLES;
  private subscription = new Subscription();

  public mobile = DeviceType.mobileType();

  public driver;
  public drivers = [];

  public uploadedFiles = new Array<any>();
  public formDataList = Array<FormData>();

  public addDriver: boolean;
  public alertSh: boolean;
  public vehicle: any = '';
  public apiPathFile = ApiPaths.VEHICLE_FILE;
  public perfis = [];
  public notifys;

  public vehicles_type = [
    {label: 'Aventureiro compacto', value: 'Aventureiro compacto'},
    {label: 'Hatch subcompacto', value: 'Hatch subcompacto'},
    {label: 'Hatch compacto', value: 'Hatch compacto'},
    {label: 'Hatch médio', value: 'Hatch médio'},
    {label: 'Sedã compacto', value: 'Sedã compacto'},
    {label: 'Sedã médio', value: 'Sedã médio'},
    {label: 'Sedã grande', value: 'Sedã grande'},
    {label: 'Familiar compacto', value: 'Familiar compacto'},
    {label: 'Familiar médio', value: 'Familiar médio'},
    {label: 'Familiar grande', value: 'Familiar grande'},
    {label: 'Picape compacta', value: 'Picape compacta'},
    {label: 'Picape média', value: 'Picape média'},
    {label: 'Picape grande', value: 'Picape grande'},
    {label: 'SUV compacto', value: 'SUV compacto'},
    {label: 'SUV médio', value: 'SUV médio'},
    {label: 'SUV grande', value: 'SUV grande'},
    {label: 'Esportivo compacto', value: 'Esportivo compacto'},
    {label: 'Esportivo médio', value: 'Esportivo médio'},
    {label: 'Esportivo grande', value: 'Esportivo grande'},
    {label: 'Conversível compacto', value: 'Conversível compacto'},
    {label: 'Conversível médio', value: 'Conversível médio'},
    {label: 'Conversível grande', value: 'Conversível grande'},
    {label: 'Van média', value: 'Van média'},
    {label: 'Van grande', value: 'Van grande'},
    {label: 'Jipe', value: 'Jipe'},
    {label: 'Furgão compacto', value: 'Furgão compacto'},
    {label: 'Furgão médio', value: 'Furgão médio'},
    {label: 'Caminhão urbano', value: 'Caminhão urbano'},
    {label: 'Trator', value: 'Trator'},
  ].sort(function (a, b) {
    return a.label < b.label ? -1 : a.label > b.label ? 1 : 0;
  }); //mock

  public fuelTypes = [
    {label: 'Gasolina', value: 'Gasolina'},
    {label: 'Etanol', value: 'Etanol'},
    {label: 'Diesel ', value: 'Diesel'},
    {label: 'GNV', value: 'GNV'},
    {label: 'Eletricidade', value: 'Eletricidade'},
  ].sort(function (a, b) {
    return a.label < b.label ? -1 : a.label > b.label ? 1 : 0;
  }); //mock

  public vehiclesTypeFunction = [
    {label: 'Escolta', value: 'Escolta'},
    {label: 'Transporte', value: 'Transporte'},
    {label: 'Outros', value: 'Outros'},
  ] // mock

  constructor(
    private formBuilder: FormBuilder,
    private msg: MsgService,
    public router: Router,
    private spinner: NgxSpinnerService,
    private service: ApiService,
    private breadcrumbService: BreadcrumbService,
    public domSanitizer: DomSanitizer
  ) {
    this.breadcrumbService.setItems([{label: 'Registrar veículo'}]);
  }

  private loadFormGroup(): void {
    this.vehicleFormGroup = this.formBuilder.group({
      name: ['', [Validators.required]],
      brand: ['', [Validators.required]],
      currentKm: ['', [Validators.required]],
      type: ['', [Validators.required]],
      fuelType: ['', [Validators.required]],
      vehicleRegistration: ['', [Validators.required]],
      yearManufacture: ['', [Validators.required]],
      yaerModel: ['', [Validators.required]],
      uf: ['', [Validators.required]],
      city: ['', [Validators.required]],
      country: ['', [Validators.required]],
      axleQuantity: ['', [Validators.required]],
      color: ['', [Validators.required]],
      consumptionCoefficient: ['', [Validators.required]],
      tankCapacity: ['', [Validators.required]],
      chassisNumber: ['', [Validators.required]],
      vehicleFunction: ['', [Validators.required]],
      description: [''],
     // driver: ['']
     },
    );

    this.detailsFormGroup = FORM_BUILDER_DETAIL(this)

    this.controlsFormGroup = this.formBuilder.group({
      controls: this.formBuilder.array([ CREATE_FORM_CONTROLS(this) ]),
    });
  }

    public cancelAlert(){
      this.alertSh = false;
      this.alertFormGroup.reset();
    }

  /* END */

  public driverCancel(){
    this.addDriver = false;
    this.driver = null;
  }

  public onClick(){
    this.loadPerfis();
 }

  public reset(stepper) {
    this.loadFormGroup();
    stepper.reset();
  }

  public searchDriver(event): void{

      this.service.get(`${ApiPaths.DRIVER}/${event.key}`, {})
                  .subscribe((r: Array<any>) => {
                    this.drivers = r;
                  });
  }

  private resetForm():void{
    this.vehicleFormGroup.reset();
 }

 public loadPerfis(){
    this.spinner.show();
    this.service.get(ApiPaths.PERFIL, {page: 0, size: 1000})
                .subscribe((r: ElementsResponse) => {

                this.perfis = r.elements.map((r) => {
                                  return {label: r.name, value: r.name};
                              });

                 this.spinner.hide();

                });
}


  public register(stepper): void{

      this.spinner.show();

     const sub = this.service.post<SimpleData>( `${ApiPaths.VEHICLE}`, BUILD_PAYLOAD_VEHICLE(this) )
                             .subscribe((r) => {
                                  console.log(r)
                                  this.vehicle = r;
                                  stepper.next()
                                  this.spinner.hide();
                                  this.msg.toast(TOAST_SUCCESSO(SAVE_SUCESS));
                                  this.resetForm();
                             });

      this.subscription.add(sub);
  }

  ngOnInit(): void {
    this.loadFormGroup();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}
