import { ValidateDate } from 'src/app/shared/utils/validateDate';
export const BUILD_PAYLOAD_EMPLOYEE_DRIVER = function buildPayload<Driver>(component) {

  handleDate(component);
  handlePhone(component);

  return {
    driversLicense: {
      ...component.cnhFormGroup.value
    },
    associedVehicles: [],
    contactReference: component.driverDetailsFormGroup.value.contactReferences,
    thirdOrAggregate: component.driverDetailsFormGroup.value.thirdOrAggregate,
    courses: component.coursesFormGroup.value.courses,
    authorization: component.authorizationFormGroup.value.authorizations,
    firstTravel: component.driverDetailsFormGroup.value.firstTravel,
  }
}

function handlePhone(component){
      component.driverDetailsFormGroup
              .value
              .contactReferences
              .forEach((r) => {
              r.contacts = [{
                               phones: r.contacts.map((p: any) => {
                                         return p.phone
                     }),
                                email: ''
            }];
          });
}

function handleDate(component){
  component.coursesFormGroup.value
                            .courses
                            .forEach((r) => {
                              r.validity = ValidateDate.parseDate(r.validity);
                            });

  component.authorizationFormGroup.value
                                  .authorizations
                                  .forEach((r) => {
                                    r.validity = ValidateDate.parseDate(r.validity);
                                  });

  component.cnhFormGroup.value.dateIssue = ValidateDate.parseDate(component.cnhFormGroup.value.dateIssue);
  component.cnhFormGroup.value.firstCnh = ValidateDate.parseDate(component.cnhFormGroup.value.firstCnh);
  component.cnhFormGroup.value.validity = ValidateDate.parseDate(component.cnhFormGroup.value.validity);
  component.driverDetailsFormGroup.value.firstTravel = ValidateDate.parseDate(component.driverDetailsFormGroup.value.firstTravel);

}
