import { BUILD_LINK_GOOGLE_MAPS } from './../../../shared/utils/build_link_googleMaps';
import { BUILD_PAYLOAD_SUPPLIER } from './build_payload';
import { LoginCookieStorage } from 'src/app/core/cookieStorage/loginCookieStorage';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { MsgService } from 'src/app/core/menssages/msg.service';
import { ApiService } from 'src/app/core/services/api.service';
import { CepService } from 'src/app/core/services/cep.service';
import { BreadcrumbService } from 'src/app/shared/config/app.breadcrumb.service';
import { DeviceType } from 'src/app/shared/utils/device-type';
import { Subscription } from 'rxjs';
import { MatStepper } from '@angular/material/stepper';
import { ValidateDoc } from 'src/app/shared/utils/validateDoc';
import { TOAST_ERROR, TOAST_SUCCESSO } from 'src/app/core/menssages/toastEvent';
import { ApiPaths } from 'src/app/shared/enuns/api-paths.enum';
import { SAVE_SUCESS } from 'src/app/core/menssages/menssagens';
import { ADD_DETAIL, FORM_BUILDER_DETAIL, REMOVE_DETAIL } from 'src/app/shared/FormBuilderControls/controls_detail_form';
import { SimpleData } from 'src/app/shared/model/simple_data';
import { Adress } from 'src/app/shared/model/adress';
@Component({
  selector: 'app-register-supplier',
  templateUrl: './register-supplier.component.html',
  styleUrls: ['./register-supplier.component.scss']
})
export class RegisterSupplierComponent implements OnInit {

  public companyFormGroup: FormGroup;
  public detailsFormGroup: FormGroup;
  public contactFormGroup: FormGroup;
  public addressFormGroup: FormGroup;

  public apiPathFile: ApiPaths.SUPPLIERS_FILE;

  public addDetail = ADD_DETAIL;
  public removeDetail = REMOVE_DETAIL;

  private subscription = new Subscription();
  public id: string;

  public phones: FormArray;
  public details: FormArray;
  public address: FormArray;

  public mobile =  DeviceType.mobileType();

  public alerts: FormArray;
  public alertSh: boolean;

  public selectedMtStatus;
  public isLinear = false;
  public stepper: MatStepper;

  constructor(
    private formBuilder: FormBuilder,
    private loginCookieStorage: LoginCookieStorage,
    private msg: MsgService,
    private spinner: NgxSpinnerService,
    private service: ApiService,
    private router: Router,
    private cep: CepService,
    private breadcrumbService: BreadcrumbService
  ) {
    this.breadcrumbService.setItems([{ label: 'Registrar Fornecedor' }]);
  }

  private loadFormGroup(): void {
    this.companyFormGroup = this.formBuilder.group({
      name: ['', [Validators.required]],
      fantasyName: ['', [Validators.required]],
      taxId: ['', [ValidateDoc.validateCNPJ]],
      stateRegistration: ['', [Validators.required]],
      municipalRegistration: ['', [Validators.required]]
     }
    );

    this.addressFormGroup = this.formBuilder.group({
      address: this.formBuilder.array([ this.createFormAddress() ]),
     }
   );

    this.detailsFormGroup = FORM_BUILDER_DETAIL(this)
  }

  public createFormPhone(): FormGroup {
    return this.formBuilder.group({
      phone: ['', [Validators.required]]
    });
  }

  public addPhone(i): void {
    this.phones = this.addressFormGroup.get('address')['controls'][i].get('phones')['controls'] as FormArray;
    this.phones.push(this.createFormPhone());
  }

  public removePhone(i): void {
    this.phones.removeAt(i);
  }

  public createFormAddress(): FormGroup {
    return this.formBuilder.group({
      postalCode: ['', [Validators.required]],
      street: ['',  [Validators.required]],
      number: ['', [Validators.required]],
      neighborhood: ['', [Validators.required]],
      complement: [''],
      city: ['', [Validators.required]],
      state: ['', [Validators.required]],
      phones: this.formBuilder.array([ this.createFormPhone() ]),
      email: [''],
      latlng: ['', [Validators.required]]
     }
    );
  }

  public buildLinkGoogleMaps(index){
    const addres: Adress =  this.addressFormGroup.get('address')['controls'][index].value;
    BUILD_LINK_GOOGLE_MAPS(addres, this);
  }

  public addPhoneAddress(): void {
    this.phones = this.addressFormGroup.get('address')['controls'].get('phones')['controls'] as FormArray;
    this.phones.push(this.createFormPhone());
  }

  public addPAddress(): void {
    this.address = this.addressFormGroup.get('address') as FormArray;
    this.address.push(this.createFormAddress());
  }

  public removeAddress(i): void {
    this.address.removeAt(i);
  }

  public cancelAlert() {
    this.alertSh = false;
  }

  public reset(stepper){
    this.loadFormGroup();
     stepper.reset();
  }

  public keyUpcep( cep, index ): void{
      if( cep.length == 9 ){
        this.spinner.show();
        this.cep.get( cep ).subscribe((r) => {
          if(!r.cep){ this.msg.toast(TOAST_ERROR('CEP não encontrado!')); }
              this.spinner.hide();
              this.addressFormGroup.get('address')['controls'][index].get('city').setValue(r.city);
              this.addressFormGroup.get('address')['controls'][index].get('neighborhood').setValue(r.neighborhood);
              this.addressFormGroup.get('address')['controls'][index].get('state').setValue(r.state);
              this.addressFormGroup.get('address')['controls'][index].get('street').setValue(r.street);
        });
      }
  }

  public register(stepper): void{

    this.spinner.show();
    const sub = this.service
                    .post<SimpleData>(`${ApiPaths.SUPPLIERS}`, BUILD_PAYLOAD_SUPPLIER(this))
                    .subscribe((r) => {
                       this.id = r.id;
          stepper.next()
        this.spinner.hide();
        this.msg.toast(TOAST_SUCCESSO(SAVE_SUCESS));
      });
    this.subscription.add(sub);
    return
  }

  ngOnInit(): void {
    this.loadFormGroup();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}
