export const BUILD_WORD_KEY = function (x){
  let s = [];
  for(let e in x){
    if(typeof x[e] === 'object' && x[e] !== null && x[e] !== ''){
      for(let y in x[e]){
        s.push(x[e][y]);
      }
    }else{
      if(x[e] !== null && x[e] !== ''){
        s.push(x[e]);
      }
    }
  }
  return s.toString().replace(',', ' ');
}
