import { BUILD_WORD_KEY } from './../../../../shared/utils/builWordKey';
import { ValidateDate } from './../../../../shared/utils/validateDate';
export const BUILD_PAYLOAD_EXPENSE = function <Expense>($this) {

  $this.managementFormGroup.value.journeyKey = $this.journeyKey;
  $this.originFormGroup.value.origin = $this.origin;
  $this.destinationFormGroup.value.destination = $this.destination;
  $this.managementFormGroup.value.company = {
    "id": "83298392",
    "name": "DR SOUZA"
  },

  $this.managementFormGroup.value.driver = {
    id: $this.driver.id,
    name: $this.driver.name
  },

  $this.managementFormGroup.value.vehicle = {
    id: $this.vehicle.id,
    name: $this.vehicle.vehicleRegistration
  },

  $this.managementFormGroup.value.customer = {
    "id": "8329839da2",
    "name": "Inup Systems"
  },

  $this.originFormGroup.value.startDate = ValidateDate.parseDate($this.originFormGroup.value.startDate);
  $this.destinationFormGroup.value.endDate = ValidateDate.parseDate($this.destinationFormGroup.value.endDate);

  let payload = {
                ...$this.originFormGroup.value,
                ...$this.destinationFormGroup.value,
                ...$this.managementFormGroup.value,
                ...$this.moneyFormGroup.value,
                byUser: {
                  id: $this.loginCookieStorage.get().id,
                  name: $this.loginCookieStorage.get().name,
                },
                wordKey: ''
               }

   payload.wordKey = BUILD_WORD_KEY(payload);

  return payload;
}
