import { ValidateDate } from './../../../../shared/utils/validateDate';
import { BUILD_PAYLOAD_ACCIDENT } from './build_payload';
import { ApiPaths } from './../../../../shared/enuns/api-paths.enum';
import { Page } from './../../../../shared/model/response/elementsResponse';
import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { MsgService } from 'src/app/core/menssages/msg.service';
import { ApiService } from 'src/app/core/services/api.service';
import { CepService } from 'src/app/core/services/cep.service';
import { BreadcrumbService } from 'src/app/shared/config/app.breadcrumb.service';
import { ElementsResponse } from 'src/app/shared/model/response/elementsResponse';
import { DeviceType } from 'src/app/shared/utils/device-type';
import { LoginCookieStorage } from 'src/app/core/cookieStorage/loginCookieStorage';
import { SimpleData } from 'src/app/shared/model/simple_data';
import { TOAST_SUCCESSO } from 'src/app/core/menssages/toastEvent';
import { Subscription } from 'rxjs';
import { SAVE_SUCESS } from 'src/app/core/menssages/menssagens';

@Component({
  selector: 'app-register-vehicle-accident',
  templateUrl: './register-vehicle-accident.component.html',
  styleUrls: ['./register-vehicle-accident.component.scss']
})
export class RegisterVehicleAccidentComponent implements OnInit {

  public alertFormGroup: FormGroup;
  public amountFormGroup: FormGroup;
  public alerts: FormArray;
  public values: FormArray;

  private subscription = new Subscription();
  public typeAccident: any;
  public typeAccidents = new Array<any>();
  public apiPathFile: ApiPaths.VEHICLE_ACCIDENT_FILE
  public vehicles = new Array<any>();
  public vehicle
  public carId: any;
  public driver: any;
  public ValidateDate =  ValidateDate
  public accidentDate: any;
  public drivers = new Array<any>();
  public pageAccident = new Page();
  public id: any;

  public alertSh: boolean;
  public mobile = DeviceType.mobileType();

  constructor(
    private formBuilder: FormBuilder,
    private msg: MsgService,
    private spinner: NgxSpinnerService,
    private service: ApiService,
    private loginCookieStorage: LoginCookieStorage,
    public router: Router,
    private cep: CepService,
    private breadcrumbService: BreadcrumbService
  ) {
    this.breadcrumbService.setItems([{label: 'Registrar Sinistro'}]);
  }

  private loadFormGroup(): void {
    this.amountFormGroup = this.formBuilder.group({
      amount: [0],
      values: this.formBuilder.array([ this.createAmount() ]),
    });
  }

      /* amounts */

      public createAmount(): FormGroup {
      return this.formBuilder.group({
        title: ['', Validators.required],
        description: [''],
        value: ['', Validators.required],
      });
    }

    public addAmount(): void {
      this.values = this.amountFormGroup.get('values') as FormArray;
      this.values.push(this.createAmount());
    }

    public removeAmount(i): void {
      this.values.removeAt(i);
      this.calculate();
    }

    public cancelAmount(){
      this.amountFormGroup.reset();
    }

    public calculate(){
      let total = this.amountFormGroup.value.values.reduce(getTotal, 0);
      function getTotal(total, item) {
        return total + item.value;
      }
      this.amountFormGroup.get('amount').setValue(total);
    }

    public searchDriver(event): void{

      this.service.get(`${ApiPaths.DRIVER}/${event.key}`, {})
                  .subscribe((r: Array<any>) => {
                    this.drivers = r;
                  });
  }

  public filter(event) {
    let query = event.query;

    this.service.get(`${ApiPaths.VEHICLE}/${query}`, {})
    .subscribe((r: Array<any>) => {
      this.vehicles = r;
    });
  }

  public onSelectAutoComplete(value: any): any {
    setTimeout(() => {
     let c: any = document.getElementById('AutoC');
     c.value = value.vehicleRegistration;
    }, 1);
    this.carId = value.id;
  }

  public scrollToEndTypeEmployee() {

    if(this.typeAccident.length < this.pageAccident.totalElements){
      this.service.get(ApiPaths.TYPE_VEHICLE_ACCIDENT, {page: this.pageAccident.nextPage, size: this.pageAccident.size})
      .subscribe((r: ElementsResponse) => {

        this.pageAccident = r.page;

        r.elements.forEach((element) => {
          this.typeAccident.push(element);
         });
      });
    }
  }

  public loadAccident(){
    this.spinner.show();
    this.service.get(ApiPaths.TYPE_VEHICLE_ACCIDENT, {page: 0, size: 10})
                .subscribe((r: ElementsResponse) => {
                   this.pageAccident = r.page;
                   this.typeAccidents = r.elements;
                     this.spinner.hide();
                });
  }

  public register(stepper?): void {
    this.spinner.show();
    const sub = this.service
                    .post<SimpleData>(`${ApiPaths.VEHICLE_ACCIDENT}`, BUILD_PAYLOAD_ACCIDENT(this))
                    .subscribe((r) => {
                       this.id = r.id;

                       stepper.next()
                       this.spinner.hide();
                       this.msg.toast(TOAST_SUCCESSO(SAVE_SUCESS));
      });
    this.subscription.add(sub);
  }

  ngOnInit(): void {
       this.loadFormGroup();
       this.loadAccident();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}

