import { FormArray, FormGroup, Validators } from '@angular/forms';

export const CREATE_FORM_DETAIL = function create($this): FormGroup {
  return $this.formBuilder.group({
    title: ['', [Validators.required]],
    note: ['', [Validators.required]],
  });
}

export const ADD_DETAIL = function add($this): void {
  $this.details = $this.detailsFormGroup.get('details') as FormArray;
  $this.details.push(CREATE_FORM_DETAIL($this));
}

export const REMOVE_DETAIL = function remove($this, i): void {
  $this.details.removeAt(i);
}

export const FORM_BUILDER_DETAIL = function formBuild($this){
  return $this.formBuilder.group({
    details: $this.formBuilder.array([ CREATE_FORM_DETAIL($this) ]),
  });
}

