import { BUILD_WORD_KEY } from './../../../shared/utils/builWordKey';
export const BUILD_PAYLOAD_VEHICLE = function buildPayload<Vehicle>($this) {

    let driver

    if($this.driver){
      const thirdOrAggregates = $this.driver.driver.typeAssociation[$this.driver.driver.typeAssociation.length -1].thirdOrAggregates;
      driver = { id: $this.driver.id,  name: $this.driver.name, thirdOrAggregates: thirdOrAggregates }
    }

    return {
      notifys: $this.notifys,
      ...$this.vehicleFormGroup.value,
      controls: $this.controlsFormGroup.value.controls,
      details: $this.detailsFormGroup.value.details,
      associedDriver: $this.driver ? driver : null,
       wordKey: BUILD_WORD_KEY($this.vehicleFormGroup.value) +
      ' '
      +
      BUILD_WORD_KEY($this.detailsFormGroup.value.details) +
      ' '
      +
      BUILD_WORD_KEY(driver)
    }
}
