import { BUILD_WORD_KEY } from './../../../shared/utils/builWordKey';

export const BUILD_PAYLOAD_SUPPLIER = function ($this) {
  let payload = {
    ...$this.companyFormGroup.value,
    byUser: {
      id: $this.loginCookieStorage.get().id,
      name: $this.loginCookieStorage.get().name,
    },
    companyData: buildCompanyData($this),

    wordKey: BUILD_WORD_KEY({ ...$this.companyFormGroup.value }),
  };

  return payload;
};

function buildCompanyData($this) {
  return $this.addressFormGroup.value.address.map((a) => {
    let location = null;

    if (a.latlng) {
      const latlng: any[] = a.latlng.replace(' ', '').split(',');
      location = { lat: latlng[0], lng: latlng[1] };
    }
    return {
      address: {
        postalCode: a.postalCode,
        street: a.street,
        number: a.number,
        neighborhood: a.neighborhood,
        complement: a.complement,
        city: a.city,
        state: a.state,
        location: location,
      },
      contact: {
        email: a.email,
        phones: a.phones.map((result) => {
          return result.phone;
        }),
      },
    };
  });
}
