import { BUILD_WORD_KEY } from '../../../../shared/utils/builWordKey';
import { ValidateDate } from '../../../../shared/utils/validateDate';

export const BUILD_PAYLOAD_EMPLOYEE = function buildPayload($this) {
  let payload = {
    ...$this.personalDataFormGroup.value,
    rg: $this.docFormGroup.value,
    type: $this.professionalDataFormGroup.value.type,
    perfil: $this.perfil,
    contact: {
      email: $this.contactFormGroup.value.email,

      phones: $this.contactFormGroup.value.phones.map((result) => {
        return result.phone;
      }),
    },
    byUser:{
     id: $this.loginCookieStorage.get().id,
     name: $this.loginCookieStorage.get().name
    },
    adress: [$this.addressFormGroup.value],
    professionalData: {
      cnpj: $this.professionalDataFormGroup.value.cnpj,
      office: [$this.office],
      note: $this.professionalDataFormGroup.value.note,
    },
    wordKey: $this.personalDataFormGroup.value.wordkey,
  };

  payload.gender = payload.gender.code;
  payload.birthday = ValidateDate.parseDate(payload.birthday);
  payload.rg.rgDateEmitter = ValidateDate.parseDate(
    payload.rg.rgDateEmitter
  );

  payload.wordKey = BUILD_WORD_KEY(payload);

  return payload;
};
