import { ImgService } from './img.service';
import { Component, OnInit } from '@angular/core';
import { Input } from '@angular/core';
import { Spinner } from 'ngx-spinner/lib/ngx-spinner.enum';
import { NgxSpinnerService } from 'ngx-spinner';
import { ApiService } from 'src/app/core/services/api.service';

@Component({
  selector: 'app-img',
  templateUrl: './img.component.html',
  styleUrls: ['./img.component.scss']
})
export class ImgComponent implements OnInit {

  @Input() stepper;
  @Input() apiPaths;
  @Input() id;

  constructor(public service: ImgService,
              private spinner: NgxSpinnerService,
              private apiService: ApiService,
              ) { }

  /* IMG */

  public onSelectImg(event): void {
    this.service.uploadedFiles = event.currentFiles;
  }

  public onClear(): void {
    this.service.uploadedFiles = [];
  }

  public sendImg(stepper){
    this.spinner.show();
    this.apiService.update<void>(`${this.apiPaths}/${this.id}`, this.service.buildFormDatas())
                .subscribe(() => {
                  this.spinner.hide();
                  stepper.next();
                })
  }

  ngOnInit(): void {
  }

}
