import { AbstractControl } from "@angular/forms";

export class ValidateDate {
public static validateDateString (value: AbstractControl) {

  let date = value.value;

  if (date == null || date == '' || date == undefined) {
    return {
      date: {
        valid: false
      }
    };
  }

  let d: any;

  if (date.indexOf('-') > -1) {
    d = date;
  } else {
    d = ValidateDate.parseDate(date);
  }

  d = new Date(d);

  if (d == 'Invalid Date') {
    return {
      date: {
        valid: false
      }
    };
  }

  return null;
};

public static invalidDate(value): boolean{
  let d: any = new Date(value);
  if (d == 'Invalid Date') {
    return true;
    };

   return false;
}

public static parseDate (date: string) {
  if(!date){
    return '';
  }
  date = date.replace(/\D+/g, '');

  let dd = date.substring(0, 2);
  let mm = date.substring(2, 4) + '-';
  let yy = date.substring(4, 9) + '-';

  return yy + mm + dd;
}

public static parseDateBrFormat (date: string) {
  date = date.replace(/\D+/g, '');

  let dd = date.substring(0, 2);
  let mm = date.substring(2, 4) + '/';
  let yy = date.substring(4, 9) + '/';

  return dd + mm  + yy;
}
}
