import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { SAVE_SUCESS } from 'src/app/core/menssages/menssagens';
import { MsgService } from 'src/app/core/menssages/msg.service';
import { TOAST_SUCCESSO } from 'src/app/core/menssages/toastEvent';
import { ApiService } from 'src/app/core/services/api.service';
import { BreadcrumbService } from 'src/app/shared/config/app.breadcrumb.service';
import { ApiPaths } from 'src/app/shared/enuns/api-paths.enum';
import { BUILD_WORD_KEY } from 'src/app/shared/utils/builWordKey';

@Component({
  selector: 'app-register-function',
  templateUrl: './register-function.component.html',
  styleUrls: ['./register-function.component.scss']
})
export class RegisterFunctionComponent implements OnInit {

  public functionFormGroup: FormGroup;

  constructor(
    private formBuilder: FormBuilder,
    private msg: MsgService,
    private spinner: NgxSpinnerService,
    private service: ApiService,
    private breadcrumbService: BreadcrumbService
  ) {
    this.breadcrumbService.setItems([{ label: 'Registrar tipo função de colaboradores'}]);
  }


  private loadFormGroup(): void {
    this.functionFormGroup = this.formBuilder.group({
      name: ['', [Validators.required]],
      description: ['', [Validators.required]],
      wordKey: [''],
    });
  }

  public register(){
    this.functionFormGroup.value.wordKey = BUILD_WORD_KEY(this.functionFormGroup.value);
    this.spinner.show();
    this.service.post(ApiPaths.TYPE_EMPLOYEE, this.functionFormGroup.value)
                .subscribe(() => {
                  this.spinner.hide();
                  this.msg.toast(TOAST_SUCCESSO(SAVE_SUCESS));
                  this.functionFormGroup.reset();
                });
  }

  ngOnInit(): void {
    this.loadFormGroup();
  }

}
