import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { SAVE_SUCESS } from 'src/app/core/menssages/menssagens';
import { MsgService } from 'src/app/core/menssages/msg.service';
import { TOAST_SUCCESSO } from 'src/app/core/menssages/toastEvent';
import { ApiService } from 'src/app/core/services/api.service';
import { BreadcrumbService } from 'src/app/shared/config/app.breadcrumb.service';
import { ApiPaths } from 'src/app/shared/enuns/api-paths.enum';
import { BUILD_WORD_KEY } from 'src/app/shared/utils/builWordKey';

@Component({
  selector: 'app-register-vehicle-accident-type',
  templateUrl: './register-vehicle-accident-type.component.html',
  styleUrls: ['./register-vehicle-accident-type.component.scss']
})
export class RegisterVehicleAccidentTypeComponent implements OnInit {

  public registerFormGroup: FormGroup;

  constructor(
    private formBuilder: FormBuilder,
    private msg: MsgService,
    private spinner: NgxSpinnerService,
    private service: ApiService,
    private breadcrumbService: BreadcrumbService
  ) {
    this.breadcrumbService.setItems([{ label: 'Registrar tipo de sinistros'}]);
  }


  private loadFormGroup(): void {
    this.registerFormGroup = this.formBuilder.group({
      name: ['', [Validators.required]],
      description: ['', [Validators.required]],
      wordKey: [''],
    });
  }

  public register(){
    this.registerFormGroup.value.wordKey = BUILD_WORD_KEY(this.registerFormGroup.value);
    this.spinner.show();
    this.service.post(ApiPaths.TYPE_VEHICLE_ACCIDENT, this.registerFormGroup.value)
                .subscribe(() => {
                  this.spinner.hide();
                  this.msg.toast(TOAST_SUCCESSO(SAVE_SUCESS));
                  this.registerFormGroup.reset();
                });
  }

  ngOnInit(): void {
    this.loadFormGroup();
  }

}
