import { Driver } from '../../../../shared/model/driver_model';
import { BUILD_PAYLOAD_EMPLOYEE_DRIVER } from './build_payload';
import { CrudRegisterDriverStorage } from '../../../../shared/storage/crudStorageDriverRegister';
import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatStepper } from '@angular/material/stepper';
import { NgxSpinnerService } from 'ngx-spinner';
import { Subscription } from 'rxjs';
import { MsgService } from 'src/app/core/menssages/msg.service';
import { ApiService } from 'src/app/core/services/api.service';
import { BreadcrumbService } from 'src/app/shared/config/app.breadcrumb.service';
import { DeviceType } from 'src/app/shared/utils/device-type';
import { TOAST_SUCCESSO } from 'src/app/core/menssages/toastEvent';
import { ApiPaths } from 'src/app/shared/enuns/api-paths.enum';
import { BUILD_PAYLOAD_EMPLOYEE } from '../register-employee/build_payload';
import { SAVE_SUCESS } from 'src/app/core/menssages/menssagens';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register-driver',
  templateUrl: './register-driver.component.html',
  styleUrls: ['./register-driver.component.scss'],
})
export class RegisterDriverComponent implements OnInit {

  public cnhFormGroup: FormGroup;
  public driverDetailsFormGroup: FormGroup;
  public contactFormGroup: FormGroup;
  public addressFormGroup: FormGroup;
  public docFormGroup: FormGroup;
  public coursesFormGroup: FormGroup;
  public authorizationFormGroup: FormGroup;
  public alertFormGroup: FormGroup;

  private subscription = new Subscription();
  public selectedMtStatus;
  public isLinear = false;
  public stepper: MatStepper;

  public contactReferences: FormArray;
  public contacts: FormArray;
  public couses: FormArray;
  public authorizations: FormArray;
  public alerts: FormArray;

  public storage = CrudRegisterDriverStorage.get();
  public apiPathFile = ApiPaths.EMPLOYEE_FILES;
  public addCours: boolean;
  public addAuth: boolean;
  public alertSh: boolean;
  public uploadedFiles = new Array<any>();
  public formDataList = Array<FormData>();
  public mobile = DeviceType.mobileType();


  public categories = [{label: 'ACC', value: 'ACC'},
                       {label: 'A', value: 'A'},
                       {label: 'B', value: 'B'},
                       {label: 'C', value: 'C'},
                       {label: 'D', value: 'D'},
                       {label: 'E', value: 'E'}]


  public options = [ {label: 'Terceiro', value: 'Terceiro'},
                     {label: 'Agregado', value: 'Agregado'} ];

  constructor(
    private formBuilder: FormBuilder,
    public router: Router,
    private msg: MsgService,
    private spinner: NgxSpinnerService,
    private service: ApiService,
    private breadcrumbService: BreadcrumbService
  ) {
    this.breadcrumbService.setItems([{ label: 'Registrar ' + CrudRegisterDriverStorage.getType() }]);
  }

  private loadFormGroup(): void {

    this.cnhFormGroup = this.formBuilder.group({
      number: ['', [Validators.required]],
      categorys: ['', [Validators.required]],
      validity: ['', [Validators.required]],
      firstCnh: ['', [Validators.required]],
      dateIssue: ['', [Validators.required]],
      mirrorNumber: ['', [Validators.required]],
      NRenach: ['', [Validators.required]],
      emittingOrgan: ['', [Validators.required]],
      securityCode: ['', [Validators.required]],
      state: ['', [Validators.required]],
    });

    this.driverDetailsFormGroup = this.formBuilder.group({
/*       note: [''],
 */      firstTravel: ['', [Validators.required]],
      thirdOrAggregate: ['', [Validators.required]],
      contactReferences: this.formBuilder.array([ this.createFormReferences() ]),
    });

    this.coursesFormGroup = this.formBuilder.group({
      courses: this.formBuilder.array([ this.createFormCurses() ]),
    });

    this.authorizationFormGroup = this.formBuilder.group({
      authorizations: this.formBuilder.array([ this.createAuthorizations() ]),
    });

    this.alertFormGroup = this.formBuilder.group({
      alerts: this.formBuilder.array([ this.createAlerts() ]),
    });
  }

  /* References */
  public createFormReferences(): FormGroup {
    return this.formBuilder.group({
      relationshipKinship: ['', [Validators.required]],
      name: ['', [Validators.required]],
      contacts: this.formBuilder.array([ this.createFormPhones() ])
    });
  }

  public addReferences(): void {
    this.contactReferences = this.driverDetailsFormGroup.get('contactReferences') as FormArray;
    this.contactReferences.push(this.createFormReferences());
  }

  public removeReferences(i): void {
    this.contactReferences.removeAt(i);
  }

  /* Phones */
  public createFormPhones(): FormGroup {
    return this.formBuilder.group({
      phone: ['', [Validators.required]],
    });
  }

  public addPhones(i): void {
    this.contacts = this.driverDetailsFormGroup.get('contactReferences')['controls'][i].get('contacts') as FormArray;
    this.contacts.push(this.createFormPhones());
  }

  public removePhone(i): void {
    this.contacts.removeAt(i);
  }

   /* Curses */

   public createFormCurses(): FormGroup {
    return this.formBuilder.group({
      type: ['', [Validators.required]],
      decription: ['', [Validators.required]],
      validity: ['']
    });
  }

  public addCurses(): void {
    this.couses = this.coursesFormGroup.get('courses') as FormArray;
    this.couses.push(this.createFormCurses());
  }

  public removeCurses(i): void {
    this.couses.removeAt(i);
  }

    /* Authorizations */

    public createAuthorizations(): FormGroup {
      return this.formBuilder.group({
        type: ['', [Validators.required]],
        decription: ['', [Validators.required]],
        number: ['', [Validators.required]],
        validity: ['', [Validators.required]]
      });
    }

    public addAuthorizations(): void {
      this.authorizations = this.authorizationFormGroup.get('authorizations') as FormArray;
      this.authorizations.push(this.createAuthorizations());
    }

    public removeAuthorizations(i): void {
      this.authorizations.removeAt(i);
    }

    public cancelAuthorization(){
      this.addAuth = false;
      this.authorizationFormGroup.reset();
    }

    /* Alerts */

    public createAlerts(): FormGroup {
      return this.formBuilder.group({
        title: ['', [Validators.required]],
        date: ['', [Validators.required]],
        note: ['', [Validators.required]],
        priorityColor: ['', [Validators.required]],
        namePriority: ['', [Validators.required]],
        perfil: [''],
        usersIds: []
      });
    }

    public addAlert(): void {
      this.alerts = this.alertFormGroup.get('alerts') as FormArray;
      this.alerts.push(this.createAlerts());
    }

    public removeAlert(i): void {
      this.alerts.removeAt(i);
    }

    public cancelAlert(){
      this.alertSh = false;
      this.alertFormGroup.reset();
    }

    /* END */

    /* IMG */

    public onSelectImg(event): void{
      this.uploadedFiles = event.currentFiles;
    }

    public onClear(): void{
    this.uploadedFiles =  [];
    }

    public buildFormDatas(stepper):void{

      const fd = new Array<FormData>()

     for(let file of this.uploadedFiles){
       const formData = new FormData()
       formData.append('file', file)
       fd.push(formData);
     }

     this.formDataList = fd;
     console.log(this.formDataList );
      stepper.next();
    }

  /* END */

    public cancelCourse(){
      this.addCours = false;
      this.coursesFormGroup.reset();
    }

  public reset() {
    this.loadFormGroup();
  //  stepper.reset();
  }

  public register(stepper?): void {

    this.spinner.show();
    const sub = this.service
                    .updateAll<void>(`${ApiPaths.DRIVER}/${CrudRegisterDriverStorage.get()}`,
                                       BUILD_PAYLOAD_EMPLOYEE_DRIVER<Driver>(this))
                    .subscribe(() => {

                      stepper.next()

                      this.spinner.hide();
                      this.msg.toast(TOAST_SUCCESSO(SAVE_SUCESS));
                    });
    this.subscription.add(sub);
  }

  ngOnInit(): void {
    this.loadFormGroup();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}
