import { BUILD_PAYLOAD_ALERT } from './build_payload';
import { CAR_REDUX } from './../../../core/redux/reducers/cartReducer';
import { Component, OnInit, Input } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { MsgService } from 'src/app/core/menssages/msg.service';
import { ApiService } from 'src/app/core/services/api.service';
import { BreadcrumbService } from 'src/app/shared/config/app.breadcrumb.service';
import { ApiPaths } from 'src/app/shared/enuns/api-paths.enum';
import { TOAST_SUCCESSO } from 'src/app/core/menssages/toastEvent';
import { SAVE_SUCESS } from 'src/app/core/menssages/menssagens';
import { LoginCookieStorage } from 'src/app/core/cookieStorage/loginCookieStorage';
import { ElementsResponse, Page } from 'src/app/shared/model/response/elementsResponse';

@Component({
  selector: 'app-alert',
  templateUrl: './alert.component.html',
  styleUrls: ['./alert.component.scss']
})
export class AlertComponent implements OnInit {

  public alertFormGroup: FormGroup;
  public alerts: FormArray;
  public page = new Page();
  public perfis = [{label: 'Apenas para mim', value: null}];

  @Input() id: string;
  @Input() vehicle: any;
  @Input() loadApp: boolean;
  @Input() stepper: any;

  constructor(
    private formBuilder: FormBuilder,
    private msg: MsgService,
    public loginCookieStorage: LoginCookieStorage,
    private spinner: NgxSpinnerService,
    private service: ApiService,
    private breadcrumbService: BreadcrumbService
  ) {
    this.breadcrumbService.setItems([{ label: 'Registrar alertas'}]);
  }

  private loadFormGroup():void{
    this.alertFormGroup = this.formBuilder.group({
      title: ['', [Validators.required]],
      note: ['', [Validators.required]],
      notifys: [''],
      carId: [''],
      vehicleRegistration: [''],
      priorityName: [''],
      priorityColor: [''],
      alertDay: ['', [Validators.required]],
    });
  }

  public onClick(){
     this.loadPerfis();
  }

  public loadPerfis(){
    if(this.perfis.length == 1){
      this.spinner.show();
      this.service.get(ApiPaths.PERFIL, {page: 0, size: 1000})
                  .subscribe((r: ElementsResponse) => {

                    r.elements.forEach((r) => {
                      this.perfis.push({label: r.name, value: r.name});
                   });

                   this.perfis = [...this.perfis];
                   this.spinner.hide();
                   this.page = r.page;

                  });
    }
  }

  public register(): void {

   this.service.post(ApiPaths.ALERT, BUILD_PAYLOAD_ALERT(this))
                .subscribe((r) => {
                  this.spinner.hide();
                  this.msg.toast(TOAST_SUCCESSO(SAVE_SUCESS));
                  this.alertFormGroup.reset();
                });
  }

  public removeAlert(i): void {
    this.alerts.removeAt(i);
  }

  ngOnInit(): void {

    this.loadFormGroup();
  }

}
