import { FormArray, FormGroup, Validators } from '@angular/forms';

export const CREATE_FORM_CONTROLS = function create(component): FormGroup {
  return component.formBuilder.group({
    title: ['', [Validators.required]],
    km: ['', [Validators.required]],
    decription: ['', [Validators.required]],
  });
}

export const ADD_CONTROL = function add(component): void {
  component.controls = component.controlsFormGroup.get('controls') as FormArray;
  component.controls.push(CREATE_FORM_CONTROLS(component));
}

export const REMOVE_CONTROL = function remove(component, i): void {
  component.controls.removeAt(i);
}
