import { ValidateDate } from './../../../shared/utils/validateDate';

export const BUILD_PAYLOAD_JOURNEY = function <Joruney>($this) {

  return {
     driver: {
         id: $this.driver.id,
         name: $this.driver.name
     },
     customer: {
      id: $this.vehicle.id,
      name: $this.vehicle.vehicleRegistration
     },
     vehicle: {
         id: $this.vehicle.id,
         name: $this.vehicle.vehicleRegistration
     },
     company: {
        id: $this.vehicle.id,
        name: "DR SOUZA"
    },
    origin: buildDestiny($this.originFormGroup),
    destiny: buildDestiny($this.destinationFormGroup),
    journeyId: $this.journeyKey,
    tariff: $this.moneyFormGroup.tariff,
    invoice: $this.moneyFormGroup.invoice,
    byUser:{
      id: $this.loginCookieStorage.get().id,
      name: $this.loginCookieStorage.get().name
     },
  }
}

function buildDestiny( form ) {

    let location = null;

    if (form.value.latlng) {
      const latlng: any[] = form.value.latlng.replace(' ', '').split(',');
      location = { lat: latlng[0], lng: latlng[1] };
    }

    return {
      day: ValidateDate.parseDate(form.value.day),
      km: form.value.km ? form.value.km : null,
      address: {
        postalCode: form.value.postalCode,
        street: form.value.street,
        number: form.value.number,
        neighborhood: form.value.neighborhood,
        complement: form.value.complement,
        city: form.value.city,
        state: form.value.state,
        location: location,
      }
    };
}
