import { FormArray, FormGroup, Validators } from '@angular/forms';

export const CREATE_FORM_AMOUNT = function ($this): FormGroup {
  return $this.formBuilder.group({
    title: ['', Validators.required],
    liters: ['', $this.typeExpense.liquid ? Validators.required : null],
    description: ['', Validators.required],
    value: ['', Validators.required],
  });
}

export const ADD_CONTROL = function ($this): void {
  $this.values = $this.amountFormGroup.get('values') as FormArray;
  $this.values.push(CREATE_FORM_AMOUNT($this));
}

export const REMOVE_CONTROL = function ($this, i): void {
  $this.values.removeAt(i);
}
