import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { MsgService } from 'src/app/core/menssages/msg.service';
import { ApiService } from 'src/app/core/services/api.service';
import { BreadcrumbService } from 'src/app/shared/config/app.breadcrumb.service';
import { ApiPaths } from 'src/app/shared/enuns/api-paths.enum';
import { MenuModel } from 'src/app/shared/menu/app.menu.model';

@Component({
  selector: 'app-register-perfil',
  templateUrl: './register-perfil.component.html',
  styleUrls: ['./register-perfil.component.scss']
})
export class RegisterPerfilComponent implements OnInit {

  public perfilFormGroup: FormGroup;
  public loadRecurses = [];
  public menuItes = [];
  public searchText = '';

  constructor(
    private formBuilder: FormBuilder,
    private msg: MsgService,
    private spinner: NgxSpinnerService,
    private service: ApiService,
    public menuModel: MenuModel,
    private breadcrumbService: BreadcrumbService
  ) {
    this.breadcrumbService.setItems([{ label: 'Registrar Perfil' }]);
  }

  private loadFormGroup(): void {
    this.perfilFormGroup = this.formBuilder.group({
      name: ['', [Validators.required]],
      description: ['', [Validators.required]],
      wordKey: [],
      menuItens: ['']
    });
  }

  public onChangeHb(event, value) {

    if (event.checked) {

      this.menuItes.push(value.key);

      for (var i = 0; i < value.items.length; i++) {
        value.items[i].show = true;
        this.menuItes.push(value.items[i].key)
      }

      for (var i = 0; i < value?.otherKeys?.length; i++) {

        value.otherKeys[i].show = true;
        this.menuItes.push(value.otherKeys[i].key);
      }

    } else {

      const keyIndex = this.menuItes.indexOf(value.key);
      this.menuItes.splice(keyIndex, 1);

      for (var i = 0; i < value.items.length; i++) {
        const keyIndex = this.menuItes.indexOf(value.items[i].key);
        if (keyIndex > -1) {
          value.items[i].show = false;
          this.menuItes.splice(keyIndex, 1);
        }

        for (var i = 0; i < value?.otherKeys.length; i++) {
          const keyIndex = this.menuItes.indexOf(value.otherKeys[i].key);
          if (keyIndex > -1) {
            value.otherKeys[i].show = false;
            this.menuItes.splice(keyIndex, 1);
          }
        }
      }
    }
  }

  public onChange(event, value) {

    if (event.checked) {
      this.menuItes.push(value.key)
    } else {
      const i = this.menuItes.indexOf(value.key);
      this.menuItes.splice(i, 1);
    }
  }

  public register() {
    this.perfilFormGroup.value.menuItens = this.menuItes;
    this.perfilFormGroup.value.wordKey = this.perfilFormGroup.value.name;

    this.spinner.show();
    this.service.post(ApiPaths.PERFIL, this.perfilFormGroup.value)
      .subscribe(() => {
        this.spinner.hide();
      });
  }

  ngOnInit(): void {
    this.loadRecurses = [...this.menuModel.model];
    this.loadFormGroup();
  }

}
