import { ValidateDate } from "src/app/shared/utils/validateDate";

export const BUILD_PAYLOAD_ALERT = function buildPayload($this) {

  if($this.id){
    $this.alertFormGroup.value.notifys = [...$this.alertFormGroup.value.notifys, $this.id]
  }

  if($this.vehicle){
    $this.alertFormGroup.value.carId = $this.vehicle.id;
    $this.alertFormGroup.value.vehicleRegistration = $this.vehicle.vehicleRegistration
  }

  let payload = {
    ...$this.alertFormGroup.value,
    byUser: {
      id: $this.loginCookieStorage.get().id,
      name: $this.loginCookieStorage.get().name,
    }
  };

  payload.alertDay = ValidateDate.parseDate(payload.alertDay);

  return payload;
};
