import { TOAST_ERROR } from '../../../app/core/menssages/toastEvent';
import { Adress } from '../../../app/shared/model/adress';

export const BUILD_LINK_GOOGLE_MAPS = function (addres: Adress, $this){

 if( !addres.city || !addres.number){
  $this.msg.toast(TOAST_ERROR("Preencha todos campos do endereço para continuar"));
  return;
 }
  window.open(`${'https://www.google.com/maps/place'}/${addres.street}, ${addres.number} - ${addres.neighborhood} - ${addres.city} - ${addres.state},${addres.postalCode},674m/data=!3m1!1e3!4m5!3m4!1s0x0:0x0!8m2!3d!4d`)
}
