import { BUILD_WORD_KEY } from './../../../../shared/utils/builWordKey';
import { ValidateDate } from './../../../../shared/utils/validateDate';
export const BUILD_PAYLOAD_ACCIDENT = function <Accident>($this) {

  let payload = {
                driver:{
                 id: $this.driver.id,
                 name: $this.driver.name
                },
                vehicle:{
                  id: $this.vehicle.id,
                  vehicleRegistration: $this.vehicle.vehicleRegistration
                 },
                 amount: $this.amountFormGroup.value.amount,
                 type:{
                  id: $this.typeAccident.id,
                  name: $this.typeAccident.name
                 },
                 date: ValidateDate.parseDate($this.accidentDate),
                  byUser: {
                    id: $this.loginCookieStorage.get().id,
                    name: $this.loginCookieStorage.get().name,
                  },
                wordKey: ''
               }

   payload.wordKey = BUILD_WORD_KEY({a: $this.driver.name, b:$this.vehicle.vehicleRegistration, c:$this.typeAccident.name});

  return payload;
}
