import { HTTP_PARAMS } from './../../../core/config/httpParams';
import { ElementsResponse } from './../../../shared/model/response/elementsResponse';
import { SAVE_SUCESS } from './../../../core/menssages/menssagens';
import { CarResponse } from './../../../shared/model/response/carResponse';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { INPUT_VALIDATE_ERROR } from 'src/app/core/menssages/menssagens';
import { MsgService } from 'src/app/core/menssages/msg.service';
import { TOAST_ERROR, TOAST_SUCCESSO } from 'src/app/core/menssages/toastEvent';
import { ApiService } from 'src/app/core/services/api.service';
import { AutoCompleteService } from 'src/app/shared/search/autocomplete';
import { ApiPaths } from 'src/app/shared/enuns/api-paths.enum';
import { Subscription } from 'rxjs';
import { BreadcrumbService } from 'src/app/shared/config/app.breadcrumb.service';

@Component({
  selector: 'app-register-km',
  templateUrl: './register-km.component.html',
  styleUrls: ['./register-km.component.scss']
})
export class RegisterKmComponent implements OnInit {

  public blockSubmit = false;
  public carList = new Array<CarResponse>();
  public openKmDisplay: boolean;
  public closeKmDisplay: boolean;
  public km: number;
  public employees = new Array<any>();
  public employee = { id: null, name: null}
  private subscription = new Subscription();

  constructor(
    private msg: MsgService,
    private spinner: NgxSpinnerService,
    private service: ApiService,
    public autocompleService: AutoCompleteService,
    private breadcrumbService: BreadcrumbService
  ) {
    this.breadcrumbService.setItems([{label: 'Registrar KM'}]);
    this.loadEmployee();
  }

  private validatePayload(): void{
    if( !this.employee.name ){
      throw new EvalError( this.msg.toast(TOAST_ERROR('Escolha um motorista para continuar')) );
    }

    if( !this.km ){
      throw new EvalError( this.msg.toast(TOAST_ERROR(INPUT_VALIDATE_ERROR)) );
    }
  }

  private buildPayloadOpenKm(): any{
    return {
      "carId": this.carList[0].id,
      "userId": '5ef5fbf44a09234df0a908b2',
      "openKm": this.km,
      "driver": this.employee
  }
 }

  private buildPayloadCloseKm(): any{
    return {
      "userId": '5ef5fbf44a09234df0a908b2',
      "closeKm": this.km
      }
  }

  private resetForm():void{
     this.km = undefined;
     this.closeKmDisplay = false;
     this.openKmDisplay = false;
  }

  public setEmploye( value ): void{
     this.employee = value;
  }

  private loadEmployee(): void{
    this.service.get<ElementsResponse>(`${ApiPaths.EMPLOYEE}`, HTTP_PARAMS.set('size', '200')
                                                                           .set('page', '0'))
                .subscribe(r => {
                   this.employees = r.elements;
                });
  }

  public setValueAutoComplete( value: CarResponse ): void{
    this.carList = [value];
  }

  public openKm(): void{

    this.validatePayload();
    this.spinner.show();

   const sub = this.service.post( `${ApiPaths.KM}`, this.buildPayloadOpenKm() )
                            .subscribe(() => {
                              this.spinner.hide();
                              this.resetForm();
                              this.msg.toast(TOAST_SUCCESSO(SAVE_SUCESS));
                             });
      this.subscription.add(sub);
  }

  public closeKm(): void{

    if( !this.km ){
      throw new EvalError( this.msg.toast(TOAST_ERROR(INPUT_VALIDATE_ERROR)) );
    }

    this.spinner.show();

   const sub = this.service.update( `${ApiPaths.KM}/5ef506af4a09234df0a908a9`, this.buildPayloadCloseKm() )
                            .subscribe(() => {
                              this.spinner.hide();
                              this.resetForm();
                              this.msg.toast(TOAST_SUCCESSO(SAVE_SUCESS));
                             });
      this.subscription.add(sub);
  }

  ngOnInit(): void {
    this.autocompleService.loadautoComplete(document);
  }
}
